import React from 'react'

import ContactForm from '../ContactForm/ContactForm'

const Contact = () => {
  return (
    <section className='contact' id='contact'>
      <h1 className='contact__title'>Get In Touch</h1>
      <ContactForm />
    </section>
  )
}

export default Contact



